import React, { Component } from "react";
import {
  ReactiveBase,
  DataSearch,
  ResultList,
  ReactiveList,
  MultiList,
  RangeSlider
} from "@appbaseio/reactivesearch";
import "./styles.css";

import Oxygen from "./Images/oxygen.jpg";
//import { ReactiveGoogleMap } from "@appbaseio/reactivemaps";

class App extends Component {
  onData(data) {
    const image =
      data.type === "Oxygen"
        ? Oxygen
        : data.type === "Ventilator"
        ? Oxygen
        : data.type === "Blood"
        ? Oxygen
        : data.type === "Vaccine"
        ? Oxygen
        : Oxygen;

    //const stars = [];
    const { address } = data;

    return (
      <ReactiveList.ResultListWrapper>
        <ResultList key={data._id}>
          <ResultList.Image src={image} />
          <ResultList.Content>
            <ResultList.Title>{data.name}</ResultList.Title>
            <ResultList.Description>
              <div>
                <p>
                  <b>Address</b>: {address}
                </p>
                <p>
                  <b>Phone</b>: {data.phone}
                </p>
                <p>
                  <b>City</b>: {data.city}
                </p>
                <p>
                  <b>Quantity</b>: {data.quantity}
                </p>
                <p>
                  <b>Availability</b>: {data.availability}
                </p>
                <p>
                  <b>Verified</b>: {data.verified}
                </p>
              </div>
            </ResultList.Description>
          </ResultList.Content>
        </ResultList>
      </ReactiveList.ResultListWrapper>
    );
  }

  render() {
    return (
      <div className="container-fluid">
        <ReactiveBase
          app="covid"
          url="https://GS0qzgiGf:d49e9caa-37c4-495e-b49f-c3162058e2d4@covid-data-rhvqwgi-arc.searchbase.io"
          //credentials="hkXdk3vcA:a32683f3-c8ad-45db-8c86-2ac2c0f45e0c"
          //type="yelp-app"
        >
          <nav className="navbar navbar-expand-lg navbar-light bg-light">
            <a className="navbar-brand" href="#">
              Covid Help
            </a>

            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              data-target="#navbarSupportedContent"
              aria-controls="navbarSupportedContent"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon" />
            </button>

            <div
              className="collapse navbar-collapse"
              id="navbarSupportedContent"
            >
              <div className="col-lg-7 dataSearch">
                <DataSearch
                  componentId="nameReactor"
                  placeholder="Search for Verified Ventilators, Blood Donors, Oxygen Suppliers"
                  dataField={[
                    "name",
                    "name.search",
                    "name.autosuggest",
                    "type",
                    "type.search",
                    "type.autosuggest",
                    "city",
                    "city.search",
                    "city.autosuggest"
                  ]}
                  searchInputId="NameSearch"
                  iconPosition="right"
                  renderError={(error) => (
                    <div>
                      Something went wrong with DataSearch
                      <br />
                      Error details
                      <br />
                      {error}
                    </div>
                  )}
                />
              </div>
              <div className="links">
                <a
                  target="_blank"
                  href="https://www.linkedin.com/in/anupam-tiwari/"
                  className="btn  link"
                >
                  <i className="fa fa-linkedin" aria-hidden="true" /> Developer
                </a>
              </div>
            </div>
          </nav>

          <div className="row">
            <div className="col-8 col-lg-3 col-md-3 col-sm-4 scroll">
              <div className="box">
                <MultiList
                  dataField="city.keyword"
                  title="City"
                  componentId="currencyReactor"
                  placeholder="Filter City"
                  showFilter={true}
                  filterLabel="City Options"
                  react={{
                    and: ["nameReactor", "cuisineReactor"]
                  }}
                />
              </div>
              <div className="box">
                <MultiList
                  dataField="type.keyword"
                  title="Required Item"
                  componentId="cuisineReactor"
                  placeholder="Required Item"
                  showFilter={true}
                  filterLabel="Required Item"
                  react={{
                    and: ["ratingsReactor", "currencyReactor", "nameReactor"]
                  }}
                  renderError={(error) => (
                    <div>
                      Something went wrong with Cuisine MultiList
                      <br />
                      Error details
                      <br />
                      {error}
                    </div>
                  )}
                />
              </div>
              <div className="box">
                <RangeSlider
                  componentId="RangeSliderSensor"
                  dataField="cost"
                  title="Average Cost"
                  range={{
                    start: 0,
                    end: 100000
                  }}
                  rangeLabels={{
                    start: "Low: 0 Rs",
                    end: "High: 100,000 Rs"
                  }}
                  react={{
                    and: ["cuisineReactor", "currencyReactor"]
                  }}
                  renderError={(error) => (
                    <div>
                      Something went wrong with RangeSlider
                      <br />
                      Error details
                      <br />
                      {error}
                    </div>
                  )}
                />
              </div>
            </div>

            <div className="col-12 col-lg-6 col-md-6 col-sm-8 scroll">
              <ReactiveList
                componentId="queryResult"
                dataField="name"
                from={0}
                size={15}
                renderItem={this.onData}
                pagination={true}
                react={{
                  and: [
                    "currencyReactor",
                    "ratingsReactor",
                    "cuisineReactor",
                    "deliveringNowReactor",
                    "bookingReactor",
                    "deliveryReactor",
                    "tableBookinReactor",
                    "nameReactor",
                    "RangeSliderSensor"
                  ]
                }}
                renderError={(error) => (
                  <div>
                    Something went wrong with ResultList!
                    <br />
                    Error details
                    <br />
                    {error}
                  </div>
                )}
              />
            </div>
            <div className="col-lg-3 col-md-3 col-sm-6"></div>
          </div>
        </ReactiveBase>
      </div>
    );
  }
}

export default App;
